import { Injectable } from '@angular/core';
import { Http, Response, Headers, RequestOptions } from '@angular/http';
import { Observable } from 'rxjs/Observable';

import { TokenService } from '../index';

@Injectable()
export class HttpBaseService {
  //public apiUrl: string = 'http://dielle002:53434/reports/api/';
  public apiUrl: string = 'http://localhost:5000/api/';
  //public apiUrl: string = 'http://192.168.0.35:53434/api/';
  protected options: RequestOptions;

  protected token: string;

  constructor(private tokenService: TokenService) {
    this.setToken();
    this.buildRequestOptions();

    this.tokenService.tokenObservable.subscribe(
      token => {
        if (!!token) {
          this.token = token;
          this.buildRequestOptions();
        }
      }
    );
  }

  setToken() {
    try {
      this.token = this.tokenService.retrieveToken();
    } catch (e) { }
  }

  protected handleError(error: Response | any) {
    try {
      this.tokenService.tokenExpired();
    } catch(e) {
    }

    let errMsg: string;
    if (error instanceof Response) {
      const body = error.json() || '';
      const err = body.error || JSON.stringify(body);
      errMsg = `${error.status} - ${error.statusText || ''} ${err}`;
    } else {
      errMsg = error.message ? error.message : error.toString();
    }
    console.error(errMsg);
    return Observable.throw(errMsg);
  }

  private buildRequestOptions(): void {
    let headers: Headers = new Headers();
    headers.append('Content-Type', 'application/json; charset=utf-8');
    headers.append('Authorization', 'Bearer ' + this.token);

    this.options = new RequestOptions({
      headers: headers
    });
  }

}



