import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Subject as RxSubject } from 'rxjs/Subject';
import { Observable } from 'rxjs/Observable';

import {
  CalendarMonthViewDay,
  CalendarDateFormatter,
  CalendarEvent,
} from 'angular-calendar';

import { subMonths, addMonths } from 'date-fns';
import { DateFormatter } from './date-formatter.provider';

import { Activity, Subject } from '../../../shared/model';

@Component({
  selector: 'rep-calendar',
  templateUrl: './calendar.component.html',
  providers: [{
    provide: CalendarDateFormatter,
    useClass: DateFormatter
  }]
})
export class CalendarComponent implements OnInit {
  private view: string = 'month';
  private viewDate: Date = new Date(2017, 7, 7);
  private selectedDay: CalendarMonthViewDay;
  private locale: string = 'it';
  private weekStartsOn: number = 1;
  private refresh: RxSubject<any> = new RxSubject();
  private events: any[];
  @Output() private dayChanged: EventEmitter<Date> = new EventEmitter<Date>();
  @Output() private monthChanged: EventEmitter<Date> = new EventEmitter<Date>();

  constructor() { }

  ngOnInit() {
  }

  ngOnChanges(changes) {
  }

  dayClicked(day: CalendarMonthViewDay): void {
    if (this.selectedDay) {
      delete this.selectedDay.cssClass;
    }
    day.cssClass = 'cal-day-selected';
    this.selectedDay = day;
    this.dayChanged.emit(day.date);
  }

  incrementMonth(): void {
    this.viewDate = addMonths(this.viewDate, 1);
    this.monthChanged.emit(this.viewDate);
    this.refresh.next();
  }

  decrementMonth(): void {
    this.viewDate = subMonths(this.viewDate, 1);
    this.monthChanged.emit(this.viewDate);
    this.refresh.next();
  }

  addBadgeTotal(day: CalendarMonthViewDay): void {
  }

  generateEvents(activities: Activity[]) {
    this.events = [];

    for (let activity of activities) {
      for (let hours = 0; hours < activity.WorkedHours; ++hours) {
        this.events.push({
          title: activity.Description,
          start: new Date(activity.Date),
          color: ''
        });
      }
    }
    this.refresh.next();
  }
}



