import { Component, OnInit } from '@angular/core';

import { SidebarToggleService } from './shared/sidebar-toggle.service';

@Component({
  selector: 'rep-dashboard',
  templateUrl: './dashboard.component.html',
})
export class DashboardComponent implements OnInit {
  private toggled : boolean;

  constructor(private toggleService: SidebarToggleService) { }

  ngOnInit() {
    this.toggleService.isToggled().subscribe(
      (toggled: boolean) => this.toggled = toggled
    );
  }

  toggle() {
    this.toggleService.toggle();
  }

}



