import { Injectable } from '@angular/core';
import { NotificationsService } from 'angular2-notifications';

@Injectable()
export class ToastService {
  public options: any = {
    timeOut: 3000,
    showProgressBar: false,
    lastOnBottom: false
  }
  private successTitle: string = 'Successo!';
  private errorTitle: string = 'Errore!';

  constructor(private notificationsService: NotificationsService) { }


  public subjectCreated(success: boolean) {
    if (success) {
      this.notificationsService.success(this.successTitle,
        'La creazione  andata a buon fine.', this.options);
    } else {
      this.notificationsService.error(this.errorTitle,
        'Un errore ha interrotto la creazione del soggetto.', this.options);
    }
  }

  public subjectUpdated(success: boolean) {
    if (success) {
      this.notificationsService.success(this.successTitle,
        'Il soggetto  stato modificato correttamente.', this.options);
    } else {
      this.notificationsService.error(this.errorTitle,
        'Un errore ha interrotto la modifica del soggetto.', this.options);
    }
  }

  public subjectDeleted(success: boolean) {
    if (success) {
      this.notificationsService.success(this.successTitle,
        'Il soggetto  stato rimosso correttamente.', this.options);
    } else {
      this.notificationsService.error(this.errorTitle,
        'Un errore ha interrotto la rimozione del soggetto.', this.options);
    }
  }


  public jobCreated(success: boolean) {
    if (success) {
      this.notificationsService.success(this.successTitle,
        'La creazione della commessa  andata a buon fine.', this.options);
    } else {
      this.notificationsService.error(this.errorTitle,
        'Un errore ha interrotto la creazione della commessa.', this.options);
    }
  }

  public jobDeleted(success: boolean) {
    if (success) {
      this.notificationsService.success(this.successTitle,
        'La commessa  stata rimossa correttamente.', this.options);
    } else {
      this.notificationsService.error(this.errorTitle,
        'Un errore ha interrotto la rimozione della commessa.', this.options);
    }
  }

  public jobUpdated(success: boolean) {
    if (success) {
      this.notificationsService.success(this.successTitle,
        'La commessa  stata modificata correttamente.', this.options);
    } else {
      this.notificationsService.error(this.errorTitle,
        'Un errore ha interrotto la modifica della commessa.', this.options);
    }
  }

  public taskCreated(success: boolean) {
    if (success) {
      this.notificationsService.success(this.successTitle,
        'La creazione del task  andata a buon fine.', this.options);
    } else {
      this.notificationsService.error(this.errorTitle,
        'Un errore ha interrotto la creazione del task.', this.options);
    }
  }

  public taskDeleted(success: boolean) {
    if (success) {
      this.notificationsService.success(this.successTitle,
        'Il task  stato rimosso correttamente.', this.options);
    } else {
      this.notificationsService.error(this.errorTitle,
        'Un errore ha interrotto la rimozione del task.', this.options);
    }
  }

  public taskUpdated(success: boolean) {
    if (success) {
      this.notificationsService.success(this.successTitle,
        'Il task  stato modificato correttamente.', this.options);
    } else {
      this.notificationsService.error(this.errorTitle,
        'Un errore ha interrotto la modifica del task.', this.options);
    }
  }

  public activityCreated(success: boolean) {
    if (success) {
      this.notificationsService.success(this.successTitle,
        'La creazione dell\'attivit  andata a buon fine.', this.options);
    } else {
      this.notificationsService.error(this.errorTitle,
        'Un errore ha interrotto la creazione dell\'attivit.', this.options);
    }
  }

  public activityDeleted(success: boolean) {
    if (success) {
      this.notificationsService.success(this.successTitle,
        'L\'attivit  stata rimossa correttamente.', this.options);
    } else {
      this.notificationsService.error(this.errorTitle,
        'Un errore ha interrotto la rimozione dell\'attivit.', this.options);
    }
  }

  public loginSuccess(success: boolean) {
    if (success) {
      this.notificationsService.success(this.successTitle,
        'L\'accesso  stato eseguito correttamente correttamente.', this.options);
    } else {
      this.notificationsService.error('Accesso fallito!',
        'Riprova o contatta un amministratore.', this.options);
    }
  }

  public typeDeleted(success: boolean) {
    if (success) {
      this.notificationsService.success(this.successTitle,
        'Il tipo  stato rimosso correttamente.', this.options);
    } else {
      this.notificationsService.error(this.errorTitle,
        'Un errore ha interrotto la rimozione del tipo.', this.options);
    }
  }

  public typeUpdated(success: boolean) {
    if (success) {
      this.notificationsService.success(this.successTitle,
        'Il tipo  stato modificato correttamente.', this.options);
    } else {
      this.notificationsService.error(this.errorTitle,
        'Un errore ha interrotto la modifica del tipo.', this.options);
    }
  }

  public typeCreated(success: boolean) {
    if (success) {
      this.notificationsService.success(this.successTitle,
        'La creazione del tipo  andata a buon fine.', this.options);
    } else {
      this.notificationsService.error(this.errorTitle,
        'Un errore ha interrotto la creazione del tipo.', this.options);
    }
  }



}



