import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

import { Subject, Job, JobType } from '../../shared/model';
import {
  SubjectService,
  JobService,
  JobTypeService,
  ToastService,
  ErrorService
} from '../../shared/services';

@Component({
  selector: 'rep-jobs',
  templateUrl: './jobs.component.html'
})
export class JobsComponent implements OnInit {
  private subjects: Subject[];
  private selectedClient: Subject = new Subject();
  private jobs: Job[];
  private jobTypes: JobType[];
  private newJob: Job = new Job();

  constructor(
    private subjectService: SubjectService,
    private jobService: JobService,
    private jobTypeService: JobTypeService,
    private toastService: ToastService,
    private router: Router,
    private errors: ErrorService
  ) { }

  ngOnInit() {
    this.subjectService.getSubjects().subscribe(
      data => {
        this.subjects = data;
        this.selectedClient = data.find(s => s.Types.indexOf('client') > -1);
        this.newJob = this.emptyJob();
        this.getJobs(this.selectedClient.Id);
      },
      error => console.log(error)
    );

    this.jobTypeService.getJobTypes().subscribe(
      data => {
        this.jobTypes = data;
      },
      error => console.log(error)
    );

    // this.jobService.printJob() {
    //
    // }

    this.errors.reset();
  }

  createJobClick() {
    if (!!this.newJob.Code) {
      this.jobService.createJob(this.newJob).subscribe(
        data => {
          this.jobs.push(data);
          this.newJob = this.emptyJob();
          this.toastService.jobCreated(true);
          this.errors.forms.createJob.codeRequired = false;
        },
        error => this.toastService.jobCreated(false)
      );
    } else {
      this.errors.forms.createJob.codeRequired = true;
      this.toastService.taskCreated(false);
    }
  }

  clientChanged(subject: Subject) {
    this.selectedClient = subject;
    this.newJob = this.emptyJob();
    this.getJobs(this.selectedClient.Id);
  }

  goToEditJob(job: Job) {
    this.jobService.jobToEdit = job;
    this.router.navigate(['jobs', job.Code]);
  }

  isEmployer(subject: Subject) {
    return subject.Types.indexOf('client') != -1 ||
      subject.Types.indexOf('organization') != -1;
  }

  private getJobs(clientId) {
    this.jobService.getJobs(clientId).subscribe(
      data => this.jobs = data,
      error => console.log(error)
    );
  }

  private emptyJob(): Job {
    let job = new Job();
    job.ClientId = this.selectedClient.Id;
    return job;
  }

}



