import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

import { JobType, TaskType, ActivityType } from '../../shared/model';
import {
  JobTypeService,
  TaskTypeService,
  ActivityTypeService,
  ToastService,
  ErrorService
} from '../../shared/services';


@Component({
  selector: 'rep-types',
  templateUrl: './types.component.html',
})
export class TypesComponent implements OnInit {
  private types: any[];
  private selectedType: string = 'job';
  private newType: any = new JobType();
  private editing: boolean = false;
  private editTypeIndex: number;

  constructor(
    private jobTypeService: JobTypeService,
    private taskTypeService: TaskTypeService,
    private activityTypeService: ActivityTypeService,
    private toastService: ToastService,
    private router: Router,
    private errors: ErrorService
  ) { }

  ngOnInit() {
    this.getTypes();
  }

  elementChanged(jobElement: string) {
    this.selectedType = jobElement;
    this.types = [];
    this.getTypes();
    this.errors.forms.createType.codeRequired = false;
  }

  openEditClick(type) {
    this.editing = true;
    this.newType = type;
  }

  noEditClick() {
    this.editing = false;

    switch (this.selectedType) {
      case 'job':
        this.newType = new JobType();
        break;
      case 'task':
        this.newType = new TaskType();
        break;
      case 'activity':
        this.newType = new ActivityType();
        break;
    }
  }

  createTypeClick() {
    if (this.selectedType != 'activity') {
      if (!this.newType.Code || !this.newType.Name) {
        this.errors.forms.createType.codeRequired = true;
        return;
      }
      if (this.selectedType == 'job') {
        this.jobTypeService.createJobType(this.newType).subscribe(
          data => {
            this.newType = new JobType();
            this.finalizeTypeCreation(data);
          },
          error => console.log(error)
        );
      } else {
        this.taskTypeService.createTaskType(this.newType).subscribe(
          data => {
            this.newType = new TaskType();
            this.finalizeTypeCreation(data);
          },
          error => console.log(error)
        );
      }
    } else {
      if (!this.newType.Name) {
        this.errors.forms.createType.codeRequired = true;
        return;
      }
      this.activityTypeService.createActivityType(this.newType).subscribe(
        data => {
          this.newType = new ActivityType();
          this.finalizeTypeCreation(data);
        },
        error => console.log(error)
      );
    }
  }

  editTypeClick() {

    if (this.selectedType != 'activity') {
      if (!this.newType.Code || !this.newType.Name) {
        this.errors.forms.createType.codeRequired = true;
        return;
      }
      if (this.selectedType == 'job') {
        this.jobTypeService.updateJobType(this.newType).subscribe(
          data => {
            this.newType = new JobType();
            this.finalizeTypeUpdate(data);
          },
          error => console.log(error)
        );
      } else {
        this.taskTypeService.updateTaskType(this.newType).subscribe(
          data => {
            this.newType = new TaskType();
            this.finalizeTypeUpdate(data);
          },
          error => console.log(error)
        );
      }
    } else {
      if (!this.newType.Name) {
        this.errors.forms.createType.codeRequired = true;
        return;
      }
      this.activityTypeService.updateActivityType(this.newType).subscribe(
        data => {
          this.newType = new ActivityType();
          this.finalizeTypeUpdate(data);
        },
        error => console.log(error)
      );
    }
  }

  deleteTypeClick() {
    switch (this.selectedType) {
      case 'job':
        this.jobTypeService.deleteJobType(this.types[this.editTypeIndex]).subscribe(
          data => {
            this.newType = new JobType();
            this.finalizeTypeDelete();
          },
          error => console.log(error)
        );
        break;
      case 'task':

        this.taskTypeService.deleteTaskType(this.types[this.editTypeIndex]).subscribe(
          data => {
            this.newType = new TaskType();
            this.finalizeTypeDelete();
          },
          error => console.log(error)
        );
        break;
      case 'activity':
        this.activityTypeService.deleteActivityType(this.types[this.editTypeIndex]).subscribe(
          data => {
            this.newType = new ActivityType();
            this.finalizeTypeDelete();
          },
          error => console.log(error)
        );
        break;
    }
  }

  private finalizeTypeCreation(data: any) {
    this.types.push(data);
    this.toastService.typeCreated(true);
    this.errors.forms.createType.codeRequired = false;
  }

  private finalizeTypeUpdate(data: any) {
    this.toastService.typeUpdated(true);
    this.errors.forms.createType.codeRequired = false;
    this.editing = false;
  }

  private finalizeTypeDelete() {
    this.types.splice(this.editTypeIndex, 1);
    this.editing = false;
    this.toastService.typeDeleted(true);
  }

  private getTypes() {
    switch (this.selectedType) {
      case 'job':
        this.newType = new JobType();
        this.jobTypeService.getJobTypes().subscribe(
          data => {
            this.types = data;
          },
          error => console.log(error)
        );
        break;
      case 'task':
        this.newType = new TaskType();
        this.taskTypeService.getTaskTypes().subscribe(
          data => {
            this.types = data;
          },
          error => console.log(error)
        );
        break;
      case 'activity':
        this.newType = new ActivityType();
        this.activityTypeService.getActivityTypes().subscribe(
          data => {
            this.types = data;
          },
          error => console.log(error)
        );
        break;
    }
  }


}



