import { Base } from './base';

import {
  plainToClass,
  deserialize,
  deserializeArray
} from 'class-transformer';

export class Site extends Base {
  SiteName: string;
  TypesCodes: string[] = [];
  Address: string;
  Number: string;
  City: string;
  ZipCode: string;
  Province: string;
  CountryId: string;
  IsActive: boolean = true;

  constructor() { super(); }

  public static fromObject(obj) {
      return plainToClass(Site, obj);
  }

  public static fromJson(json): Site {
    return deserialize(Site, json);
  }

  public static fromJsonArray(json): Site[] {
    return deserializeArray(Site, json);
  }
}



