import { Injectable } from '@angular/core';
import { BehaviorSubject, Subject } from 'rxjs';

@Injectable()
export class TokenService {
  private tokenKey: string = 'rep_token';
  public tokenObservable: Subject<string> = new BehaviorSubject<string>(null);

  constructor() { }

  storeToken(content) {
    localStorage.setItem(this.tokenKey, JSON.stringify(content));
    this.tokenObservable.next(content.access_token);
  }

  retrieveToken() {
    let storedToken: string = localStorage.getItem(this.tokenKey);
    if (!storedToken) {
      throw new Error('token not found');
    }
    return JSON.parse(storedToken).access_token;
  }

  removeToken() {
    localStorage.clear();
  }

  getTokenPayload() {
    let token = this.retrieveToken();
    var base64Url = token.split('.')[1];
    var base64 = base64Url.replace('-', '+').replace('_', '/');
    return JSON.parse(window.atob(base64));
  };

  tokenExpired() {
    let payload = this.getTokenPayload();

    if(payload.exp < Date.now()/1000) {
      this.removeToken();
      throw Error('token expired');
    }
  }

}



