import { Base } from './';

import {
  plainToClass,
  deserialize,
  deserializeArray
} from 'class-transformer';

export class TaskType extends Base {
  public Id: string;
  public Code: string;
  public Name: string;

  constructor() {
    super();
  }

  public static fromObject(obj) {
      return plainToClass(TaskType, obj);
  }

  public static fromJson(json): TaskType {
    return deserialize(TaskType, json);
  }

  public static fromJsonArray(json): TaskType[] {
    return deserializeArray(TaskType, json);
  }

}



