import { Injectable } from '@angular/core';
import { Response, Http, Headers } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';

import { ActivityType } from '../../model';
import { HttpBaseService } from './http-base.service';
import { TokenService } from '../index';

@Injectable()
export class ActivityTypeService extends HttpBaseService {

  private activityTypesUrl: string = this.apiUrl + 'activitytypes/';

  constructor(
    private http: Http,
    tokenService: TokenService,
  ) { super(tokenService) }


  getActivityTypes(): Observable<ActivityType[]> {
    return this.http.get('assets/activitytype.json', this.options)
      .map((res: Response) => ActivityType.fromObject(res.json()))
      .catch((res: Response) => this.handleError(res));
  }

  updateActivityType(type: ActivityType) {
    return this.http.put(this.activityTypesUrl, type, this.options)
      .catch((res: Response) => this.handleError(res));
  }

  createActivityType(type: ActivityType): Observable<ActivityType> {
    return this.http.post(this.activityTypesUrl, type, this.options)
      .map((res: Response) => ActivityType.fromObject(res.json()))
      .catch((res: Response) => this.handleError(res));
  }

  deleteActivityType(type: ActivityType) {
    return this.http.delete(this.activityTypesUrl + type.Id, this.options)
      .map((res: Response) => res)
      .catch((res: Response) => this.handleError(res));
  }
}



