import { Pipe, PipeTransform } from '@angular/core';

import { Subject } from '../shared/model';

@Pipe({
  name: 'subjectFilter',
  pure: false
})
export class SubjectFilterPipe implements PipeTransform {

  transform(subjects: Subject[], type?: string): Subject[] {
    if (!subjects) return [];
    switch(type) {
      case 'employees': return subjects.filter(subject => this.isEmployee(subject));
      case 'contractors': return subjects.filter(subject => this.isContractor(subject));
      case 'clients': return subjects.filter(subject => this.isClient(subject));
      case 'organizations': return subjects.filter(subject => this.isOrganization(subject));
      case 'contracting_parties': return subjects.filter(subject => this.isContractingParty(subject));
      case 'salaried': return subjects.filter(subject => this.isSalaried(subject));
      case 'employers': return subjects.filter(subject => this.isEmployer(subject));
      default: return subjects;
    }
  }

  isEmployee(subject: Subject) {
    return subject.Types.indexOf('employee') != -1;
  }

  isContractor(subject: Subject) {
    return subject.Types.indexOf('contractor') != -1;
  }

  isClient(subject: Subject) {
    return subject.Types.indexOf('client') != -1;
  }

  isOrganization(subject: Subject) {
    return subject.Types.indexOf('organization') != -1;
  }

  isContractingParty(subject: Subject) {
    return subject.Types.indexOf('contracting_party') != -1;
  }

  isSalaried(subject: Subject) {
    return this.isEmployee(subject) || this.isContractor(subject);
  }

  isEmployer(subject: Subject) {
    return this.isClient(subject) || this.isOrganization(subject);
  }

}



