import { Base } from './base';

import {
  plainToClass,
  deserialize,
  deserializeArray
} from 'class-transformer';

export class Activity extends Base {
  public Id: string;
  public ParentId: string;
  public TypeId: string;
  public Description: string;
  public EmployeeId: string;
  public Date: Date;
  public WorkedHours: number;

  // FlatActivity fields
  public ClientName: string;
  public ContractingName: string;
  public JobCode: string;
  public JobDescription: string;
  public TaskCode: string;
  public TaskDescription: string;

  constructor() {
    super();
  }

  public static fromObject(obj) {
      return plainToClass(Activity, obj);
  }

  public static fromJson(json): Activity {
    return deserialize(Activity, json);
  }

  public static fromJsonArray(json): Activity[] {
    return deserializeArray(Activity, json);
  }

}



