import { Injectable } from '@angular/core';
import { Response, Http } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';

import { Job } from '../../model';
import { HttpBaseService } from './http-base.service';
import { TokenService } from '../index';

@Injectable()
export class JobService extends HttpBaseService {
  jobToEdit: Job;

  private jobsUrl: string = this.apiUrl + 'jobs/';

  constructor(
    private http: Http,
    tokenService: TokenService,
  ) { super(tokenService) }

  getJobs(clientId: string = null): Observable<Job[]> {
    return this.http.get('assets/jobs.json', this.options)
      .map((res: Response) => Job.fromObject(res.json()))
      .catch((res: Response) => this.handleError(res));
  }

  updateJob(job: Job) {
    return this.http.put(this.jobsUrl, job, this.options)
      .map((res: Response) => Job.fromObject(res.json()))
      .catch((res: Response) => this.handleError(res));
  }

  createJob(job: Job): Observable<Job> {
    return this.http.post(this.jobsUrl, job, this.options)
      .map((res: Response) => Job.fromObject(res.json()))
      .catch((res: Response) => this.handleError(res));
  }

  deleteJob(job: Job) {
    return this.http.delete(this.jobsUrl + job.Id, this.options)
      .catch((res: Response) => this.handleError(res));
  }

  printJob(job: Job) {
    return this.http.post(this.jobsUrl, job, this.options)
      .map((res: Response) => console.log(res))
      .catch((res: Response) => this.handleError(res));
  }
}



