import { Component, OnInit, ViewChild } from '@angular/core';
import { CalendarComponent } from './calendar/calendar.component';

import {
  Subject,
  Activity,
  ActivityType,
  Job,
  Task
} from '../../shared/model';

import {
  SubjectService,
  ActivityService,
  ActivityTypeService,
  JobService,
  ErrorService,
  ToastService
} from '../../shared/services';

@Component({
  selector: 'rep-activities',
  templateUrl: './activities.component.html'
})
export class ActivitiesComponent implements OnInit {
  @ViewChild(CalendarComponent)
  private calendar: CalendarComponent;
  private selectedDay: Date;
  private selectedMonth: Date;

  private activities: Activity[];

  private subjects: Subject[];
  private jobs: Job[] = [];
  private selectedEmployee: Subject = new Subject();
  private selectedClient: Subject = new Subject();
  private selectedJob: Job = new Job();
  private selectedTask: Task;
  private selectedSubtask: Task;
  private activityTypes: ActivityType[] = [];
  private newActivity: Activity = this.emptyActivity();

  constructor(
    private subjectService: SubjectService,
    private activityService: ActivityService,
    private jobService: JobService,
    private activityTypeService: ActivityTypeService,
    private toastService: ToastService,
    private errors: ErrorService
  ) { }

  ngOnInit() {
    this.subjectService.getSubjects().subscribe(
      data => {
        this.subjects = data;
        this.selectedEmployee = this.subjects[0];
        this.newActivity.EmployeeId = this.selectedEmployee.Id;
        this.getActivities();
      },
      error => console.log(error)
    );
    this.activityTypeService.getActivityTypes().subscribe(
      data => this.activityTypes = data,
      error => console.log(error)
    );
    this.jobService.getJobs().subscribe(
      data => this.jobs = data,
      error => console.log(error)
    );

    this.errors.reset();
  }

  employeeChanged(subject: Subject) {
    this.selectedEmployee = subject;
    this.newActivity.EmployeeId = subject.Id;
    this.getActivities();
  }

  clientChanged(client: Subject) {
    this.getJobs(client.Id);
    this.selectedTask = undefined;
    this.selectedSubtask = undefined;
  }

  jobChanged(job: Job) {
    this.newActivity.ParentId = job.Id;
    this.selectedTask = undefined;
    this.selectedSubtask = undefined;
  }

  taskChanged(task: Task) {
    this.newActivity.ParentId =
      !!task.Id ? task.Id : this.selectedJob.Id;
    this.selectedSubtask = undefined;
  }

  subtaskChanged(task: Task) {
    this.newActivity.ParentId =
      !!task.Id ? task.Id : this.selectedTask.Id;
  }

  activityTypeChanged(activityType: ActivityType) {
    if (!this.newActivity.Description)
      this.newActivity.Description = activityType.Name;
  }


  dayChanged(day: Date) {
    this.selectedDay = day;
    this.newActivity.Date = day;
  }

  monthChanged(month: Date) {
    this.selectedMonth = month;
    this.getActivities();
  }

  createActivityClick() {
    console.log(this.newActivity);
    if (!this.newActivity.Description || !this.newActivity.WorkedHours) {
      this.errors.forms.createActivity.emptyFields = true;
    } else {
      this.activityService.createActivity(this.newActivity).subscribe(
        data => {
          console.log(data);
          this.activities.push(data);
          this.calendar.generateEvents(this.activities);
          this.errors.forms.createActivity.emptyFields = false;
          this.toastService.activityCreated(true);
        },
        error => {
          this.errors.forms.createActivity.emptyFields = false;
          this.toastService.activityCreated(false);
        }
      );
    }
  }

  deleteActivityClick(activity: Activity, index: number) {
    this.activityService.deleteActivity(activity).subscribe(
      () => {
        this.activities.splice(index, 1);
        this.toastService.activityDeleted(true);
        this.calendar.generateEvents(this.activities);
      },
      error => this.toastService.activityDeleted(false)
    );
  }

  isSelectedDay(isoDate: string) {
    let date = new Date(isoDate);
    return this.selectedDay.toDateString() == date.toDateString();
  }

  emptyActivities() {
    let date = this.selectedDay;
    return this.activities.filter(
      function(entry) {
        return date.toDateString() == new Date(entry.Date).toDateString();
      }
    ).length == 0;
  }

  private emptyActivity(): Activity {
    let activity = new Activity();
    activity.TypeId = 'board_machine';
    activity.EmployeeId = this.selectedEmployee.Id;

    return activity;
  }

  private getActivities() {
    let date: Date = this.selectedMonth ? this.selectedMonth : new Date();
    this.activityService.getFlatActivitiesByMonth(this.selectedEmployee, date).subscribe(
      data => {
        this.activities = data;
        this.calendar.generateEvents(this.activities);
      },
      error => console.log(error)
    );
  }

  private getJobs(clientId: string) {
    this.jobService.getJobs(clientId).subscribe(
      data => {
        this.jobs = data;
        this.selectedJob = this.jobs[0];
        this.jobChanged(this.selectedJob);
      },
      error => console.log(error)
    );
  }

}



