import { Injectable } from '@angular/core';
import { Response, Http, Headers } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';

import { TaskType } from '../../model';
import { HttpBaseService } from './http-base.service';
import { TokenService } from '../index';

@Injectable()
export class TaskTypeService extends HttpBaseService {

  private taskTypesUrl: string = this.apiUrl + 'tasktypes/';

  constructor(
    private http: Http,
    tokenService: TokenService,
  ) { super(tokenService) }


  getTaskTypes(): Observable<TaskType[]> {
    return this.http.get('assets/tasktypes.json', this.options)
      .map((res: Response) => TaskType.fromObject(res.json()))
      .catch((res: Response) => this.handleError(res));
  }

  updateTaskType(type: TaskType) {
    return this.http.put(this.taskTypesUrl, type, this.options)
      .catch((res: Response) => this.handleError(res));
  }

  createTaskType(type: TaskType): Observable<TaskType> {
    return this.http.post(this.taskTypesUrl, type, this.options)
      .map((res: Response) => TaskType.fromObject(res.json()))
      .catch((res: Response) => this.handleError(res));
  }

  deleteTaskType(type: TaskType) {
    return this.http.delete(this.taskTypesUrl + type.Id, this.options)
      .map((res: Response) => res)
      .catch((res: Response) => this.handleError(res));
  }
}



