import { ModuleWithProviders } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { DashboardComponent } from './dashboard.component';
import { SubjectsComponent } from './pages/subjects/subjects.component'
import { SubjectEditComponent } from './pages/subjects/subject-edit/subject-edit.component'
import { JobsComponent } from './pages/jobs/jobs.component';
import { TasksComponent } from './pages/jobs/tasks/tasks.component';
import { ActivitiesComponent } from './pages/activities/activities.component';
import { InsertComponent } from './pages/insert/insert.component';
import { TypesComponent } from './pages/types/types.component';

const dashboardRoutes: Routes = [
  {
    path: '',
    component: DashboardComponent,
    children: [
      {
        path: '',
        redirectTo: 'subjects',
        pathMatch: 'full'
      },
      {
        path: 'subjects',
        component: SubjectsComponent,

      },
      {
        path: 'subjects/:name',
        component: SubjectEditComponent
      },
      {
        path: 'jobs',
        component: JobsComponent,

      },
      {
        path: 'jobs/:code',
        component: TasksComponent,

      },
      {
        path: 'types',
        component: TypesComponent,

      },
      {
        path: 'activities',
        component: ActivitiesComponent,

      },
      {
        path: 'insert',
        component: InsertComponent,

      },
    ]
  },
];

export const DashboardRoutingModule: ModuleWithProviders =
  RouterModule.forChild(dashboardRoutes);



