import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { SimpleNotificationsModule } from 'angular2-notifications';

import { SubjectsComponent } from './subjects/subjects.component';
import { TranslateSubjectTypePipe } from './subjects/translate-subject-type.pipe';
import { SubjectEditComponent } from './subjects/subject-edit/subject-edit.component';
import { SubjectFormComponent } from './subjects/subject-form/subject-form.component';
import { TranslateSiteTypePipe } from './subjects/subject-form/translate-site-type.pipe';
import { JobsComponent } from './jobs/jobs.component';
import { ActivitiesComponent } from './activities/activities.component';
import { CalendarModule } from 'angular-calendar';
import { CalendarComponent } from './activities/calendar/calendar.component';
import { SubjectFilterPipe } from './subject-filter.pipe';
import { TasksComponent } from './jobs/tasks/tasks.component';
import { InsertComponent } from './insert/insert.component';
import { TypesComponent } from './types/types.component';
import { TranslateJobElementPipe } from './types/translate-job-element.pipe';
import { OrderModule } from 'ngx-order-pipe';

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    BrowserAnimationsModule,
    CalendarModule.forRoot(),
    SimpleNotificationsModule.forRoot(),
    OrderModule
  ],
  declarations: [
    SubjectsComponent,
    TranslateSubjectTypePipe,
    SubjectEditComponent,
    SubjectFormComponent,
    TranslateSiteTypePipe,
    JobsComponent,
    ActivitiesComponent,
    CalendarComponent,
    SubjectFilterPipe,
    TasksComponent,
    InsertComponent,
    TypesComponent,
    TranslateJobElementPipe
  ]
})
export class PagesModule { }



