import {
  Component,
  OnInit,
  OnChanges,
  Input,
  Output,
  EventEmitter
} from '@angular/core';

import { Location } from '@angular/common';
import { Router } from '@angular/router';

import { Site, Subject } from '../../../shared/model';
import { SubjectService, ToastService } from '../../../shared/services';

@Component({
  selector: 'rep-subject-form',
  templateUrl: './subject-form.component.html'
})
export class SubjectFormComponent implements OnInit, OnChanges {
  @Input() private edit: boolean;
  @Input() private selectedType: string;
  @Input() private subject: Subject;
  @Output() private subjectCreated : EventEmitter <Subject>;
  @Output() private subjectDeleted : EventEmitter<void>;
  private subjects: Subject[] = [];
  private openedSite: number;

  private SITE_TYPES: string[] = ['legal_site', 'logistic_site', 'residence'];
  private SUBJECT_TYPES: string[] = ['employee', 'contractor', 'contracting_party', 'client'];

  constructor(
    private subjectService: SubjectService,
    private toastService: ToastService
  ) {
    this.subjectCreated = new EventEmitter<Subject>();
    this.subjectDeleted = new EventEmitter<void>();
  }

  ngOnInit() {
    if (!this.subject) {
      this.subject = new Subject();
    }

    this.subjectService.getSubjects().subscribe(
      data => this.subjects = data,
      error => console.log(error)
    );
  }

  ngOnChanges(changes) {
    if (!this.edit) {
      this.manageSubjectTypeChange(changes.selectedType.currentValue,
        changes.selectedType.previousValue);
    }
  }

  employerChanged(employerId: string) {
    this.subject.EmployerId = employerId;
  }

  addSiteClick() {
    // TODO: check if existing sites are filled before creating a new openedSite

    if (!this.subject.Sites) {
      this.subject.Sites = [];
    }

    this.openedSite =
      this.subject.Sites.push(new Site()) - 1;
  }

  deleteSiteClick(index) {
    this.subject.Sites.splice(index, 1);
  }

  createSubjectClick() {
    this.subjectService.createSubject(this.subject).subscribe(
      data => {
        this.subject = new Subject();
        this.manageSubjectTypeChange(this.selectedType);
        this.subjectCreated.emit(data);
        this.toastService.subjectCreated(true);
      },
      error => this.toastService.subjectCreated(false)
    );
  }

  updateSubjectClick() {
    this.subjectService.updateSubject(this.subject).subscribe(
      data => this.toastService.subjectUpdated(true),
      error => this.toastService.subjectUpdated(false)
    );
  }

  deleteSubjectClick() {
    this.subjectService.deleteSubject(this.subject).subscribe(
      data => {
        this.subjectDeleted.emit();
        this.toastService.subjectDeleted(true);
      },
      error => {this.toastService.subjectDeleted(false); console.log(error)}
    );
  }

  removeEmptySites() {
    for (let i in this.subject.Sites) {
      if (!this.subject.Sites[i].SiteName)
        this.subject.Sites.splice(parseInt(i), 1);
    }
  }

  updateTypesArray(type, array, event) {
    let index = array.indexOf(type);

    if (event.target.checked && index == -1) {
      array.push(type);
    }
    else if (!event.target.checked && index != -1) {
      array.splice(index, 1);
    }
  }

  manageSubjectTypeChange(currentType, previousType=null) {
    if (!this.subject) {
      this.subject = new Subject();
    }

    if (this.subject.Types.indexOf(currentType) == -1) {
      this.subject.Types.push(currentType);
    }

    let previousTypeIndex = this.subject.Types.indexOf(previousType);
    if (previousTypeIndex != -1) {
      this.subject.Types.splice(previousTypeIndex, 1);
    }
  }



}



