import { Base } from './base';

import {
  plainToClass,
  deserialize,
  deserializeArray
} from 'class-transformer';

export class ActivityType extends Base {
  public Id: string;
  public Name: string;

  constructor() {
    super();
  }

  public static fromObject(obj) {
      return plainToClass(ActivityType, obj);
  }

  public static fromJson(json): ActivityType {
    return deserialize(ActivityType, json);
  }

  public static fromJsonArray(json): ActivityType[] {
    return deserializeArray(ActivityType, json);
  }

}



