import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Location } from '@angular/common';

import { Job, Task, Subject, JobType, TaskType } from '../../../shared/model';

import {
  SubjectService,
  JobService,
  JobTypeService,
  TaskTypeService,
  ErrorService,
  ToastService
} from '../../../shared/services';

@Component({
  selector: 'rep-tasks',
  templateUrl: './tasks.component.html'
})
export class TasksComponent implements OnInit {
  private job: Job;
  private subjects: Subject[];
  private jobTypes: JobType[];
  private taskTypes: TaskType[];
  private newTask: Task = new Task();
  private editTaskIndex: number;
  private editTaskOpen: boolean = false;
  private editSubtaskIndex: number = -1;

  constructor(
    private subjectService: SubjectService,
    private jobService: JobService,
    private jobTypeService: JobTypeService,
    private taskTypeService: TaskTypeService,
    private toastService: ToastService,
    private errors: ErrorService,
    private router: Router,
    private location: Location
  ) { }

  ngOnInit() {
    if (this.jobService.jobToEdit == undefined) {
      this.job = new Job();
      this.router.navigate(['jobs']);
    } else {
      this.job = this.jobService.jobToEdit;

      this.subjectService.getSubjects().subscribe(
        data => this.subjects = data,
        error => console.log(error)
      );
      this.jobTypeService.getJobTypes().subscribe(
        data => this.jobTypes = data,
        error => console.log(error)
      );
      this.taskTypeService.getTaskTypes().subscribe(
        data => this.taskTypes = data,
        error => console.log(error)
      );

      this.errors.reset();
    }
  }

  updateJobClick() {
    if (!!this.job.Code) {
      this.jobService.updateJob(this.job).subscribe(
        data => {
          this.toastService.jobUpdated(true)
          this.editSubtaskIndex = -1;
        },
        error => this.toastService.jobUpdated(false)
      );

      this.errors.forms.updateJob.codeRequired = false;
    } else {
      this.errors.forms.updateJob.codeRequired = true;
    }
  }

  deleteJobClick() {
    this.jobService.deleteJob(this.job).subscribe(
      () => {
        this.toastService.jobDeleted(true);
        this.goBack();
      },
      error => this.toastService.jobDeleted(false)
    );
  }

  createTaskClick() {
    if (!!this.newTask.Code) {
      this.job.Tasks.push(this.newTask);
      this.newTask = new Task();
      this.errors.forms.createTask.codeRequired = false;
    } else {
      this.errors.forms.createTask.codeRequired = true;
    }
  }

  deleteTaskClick(task: Task) {
    task.IsDeleted = true;
  }

  deleteSubtaskClick(subtask: Task) {
    subtask.IsDeleted = true;
  }

  addSubtaskClick() {
    // TODO: check if existing sites are filled before creating a new openedSite
    if (!this.job.Tasks[this.editTaskIndex].Subtasks)
      this.job.Tasks[this.editTaskIndex].Subtasks = [];

    this.job.Tasks[this.editTaskIndex].Subtasks.push(new Task());
    this.editSubtaskIndex = this.job.Tasks[this.editTaskIndex].Subtasks.length - 1;
  }

  goBack() {
    this.location.back();
  }

}



