import { Base, Activity } from '.';

import {
  Type,
  plainToClass,
  deserialize,
  deserializeArray
} from 'class-transformer';

export class Task extends Base {
  public Id: string;
  public Code: string;
  public TypeId: string;
  public ParentId: string;
  public Description: string;
  public EstimatedHours: number;
  public WorkedHours: number;
  public InvoicedHours: number;
  public IsDeleted : boolean = false;
  @Type(() => Activity)
  public Activities : Activity[] = [];
  @Type(() => Task)
  public Subtasks: Task[] = [];

  constructor() {
    super();
  }

  public static fromObject(obj) {
      return plainToClass(Task, obj);
  }

  public static fromJson(json): Task {
    return deserialize(Task, json);
  }

  public static fromJsonArray(json): Task[] {
    return deserializeArray(Task, json);
  }

}



