import { Base } from './base';
import { Site } from './site';

import {
  Type,
  plainToClass,
  deserialize,
  deserializeArray
} from 'class-transformer';

export class Subject extends Base {
  public Id: string;
  public FirstName: string;
  public LastName: string;
  public BusinessName: number;
  public TaxCode: string;
  public VATNumber: string;
  public Types: string[] = [];
  public EmployerId: string;
  public HourlyRate: number;
  public IsActive: boolean = true;
  public CreatedOn: string;
  public UpdatedOn: string;
  @Type(() => Site)
  public Sites: Site[] = [];

  constructor() {
    super();
  }

  public static fromObject(obj) {
      return plainToClass(Subject, obj as Object);
  }

  public static fromJson(json): Subject {
    return deserialize(Subject, json);
  }

  public static fromJsonArray(json): Subject[] {
    return deserializeArray(Subject, json);
  }

}



