import {
  serialize,
  classToPlain,
  classToClass
} from 'class-transformer';

export abstract class Base {
  public toJson(): string {
    return serialize(this);
  }

  public toObject(): any {
    return classToPlain(this);
  }

  public clone(): Base {
    return classToClass(this, {'ignoreDecorators': true});
  }

}



