import { Base } from '.';

import {
  plainToClass,
  deserialize,
  deserializeArray
} from 'class-transformer';

export class JobType extends Base {
  public Id: string;
  public Code: string;
  public Name: string;

  constructor() {
    super();
  }

  public static fromObject(obj) {
      return plainToClass(JobType, obj);
  }

  public static fromJson(json): JobType {
    return deserialize(JobType, json);
  }

  public static fromJsonArray(json): JobType[] {
    return deserializeArray(JobType, json);
  }

}



