import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

import { Site, Subject } from '../../shared/model';
import { SubjectService } from '../../shared/services';

@Component({
  selector: 'rep-subjects',
  templateUrl: './subjects.component.html'
})
export class SubjectsComponent implements OnInit {
  private selectedType: string = 'employee';
  private openedSite: number = 0;

  private subjects: Subject[];
  private newSubject: Subject;

  constructor(
    private subjectService: SubjectService,
    private router: Router
  ) {
    // Initialize newSubject for the form
    this.newSubject = new Subject();
  }

  ngOnInit() {
    this.subjectService.getSubjects().subscribe(
      data => this.subjects = data,
      error => console.log(error)
    );
  }

  addSiteForm() {
    if (!this.newSubject.Sites)
      this.newSubject.Sites = [];

    // TODO: check empty sites creations
    this.openedSite =
      this.newSubject.Sites.push(new Site()) - 1;

  }

  goToEditSubject(subject: Subject) {
    this.subjectService.subjectToEdit = subject;
    this.router.navigate(['subjects', subject.Id]);
  }

  subjectCreated(newSubject: Subject) {
    this.subjects.push(newSubject);
  }

}



