import { Injectable } from '@angular/core';
import { Response, Http, Headers } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';

import { JobType } from '../../model';
import { HttpBaseService } from './http-base.service';
import { TokenService } from '../index';

@Injectable()
export class JobTypeService extends HttpBaseService {

  private jobTypesUrl: string = this.apiUrl + 'jobtypes/';

  constructor(
    private http: Http,
    tokenService: TokenService,
  ) { super(tokenService) }


  getJobTypes(): Observable<JobType[]> {
    return this.http.get('assets/jobtypes.json', this.options)
      .map((res: Response) =>  JobType.fromObject(res.json()))
      .catch((res: Response) => this.handleError(res));
  }

  updateJobType(type: JobType) {
    return this.http.put(this.jobTypesUrl, type, this.options)
      .catch((res: Response) => this.handleError(res));
  }

  createJobType(type: JobType): Observable<JobType> {
    return this.http.post(this.jobTypesUrl, type, this.options)
      .map((res: Response) => JobType.fromObject(res.json()))
      .catch((res: Response) => this.handleError(res));
  }

  deleteJobType(type: JobType) {
    return this.http.delete(this.jobTypesUrl + type.Id, this.options)
      .map((res: Response) => res)
      .catch((res: Response) => this.handleError(res));
  }
}



