import { Injectable } from '@angular/core';
import { Response, Http } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';

import { Activity, ActivityType, Subject } from '../../model';
import { HttpBaseService } from './http-base.service';
import { TokenService } from '../index';

@Injectable()
export class ActivityService extends HttpBaseService {
  private activityUrl: string = this.apiUrl + 'flatactivities/';
  private flatActivityUrl: string = this.apiUrl + 'flatactivitiesbymonth/';

  constructor(
    private http: Http,
    tokenService: TokenService,
  ) { super(tokenService) }

  getActivities(employee: Subject, month: Date): Observable<Activity[]> {
    return this.http.get('assets/activities.json', this.options)
      .map((res: Response) => Activity.fromObject(res.json()))
      .catch((res: Response) => this.handleError(res));
  }

  updateActivity(activity: Activity) {
    return this.http.put(this.activityUrl, activity, this.options)
      .map((res: Response) => res.json())
      .catch((res: Response) => this.handleError(res));
  }

  createActivity(activity: Activity): Observable<Activity> {
    return this.http.post(this.activityUrl, activity, this.options)
      .map((res: Response) => Activity.fromObject(res.json()))
      .catch((res: Response) => this.handleError(res));
  }

  deleteActivity(activity: Activity) {
    return this.http.delete(this.activityUrl + activity.Id, this.options)
      .catch((res: Response) => this.handleError(res));
  }

  getFlatActivitiesByMonth(employee: Subject, month: Date): Observable<Activity[]> {
    return this.http.get('assets/activities.json', this.options)
      .map((res: Response) => Activity.fromObject(res.json()))
      .catch((res: Response) => this.handleError(res));
  }
}



