import { Base, Activity, Task } from '.';

import {
  Type,
  plainToClass,
  deserialize,
  deserializeArray
} from 'class-transformer';

export class Job extends Base {
  public Id: string;
  public Code: string;
  public TypeId: string;
  public Description: string;
  public IsActive: boolean = true;
  public ClientId: string;
  public ContractingPartyId: string;
  public EstimatedHours: number;
  public WorkedHours: number;
  public InvoicedHours: number;
  @Type(() => Activity)
  public Activities: Activity[] = [];
  @Type(() => Task)
  public Tasks: Task[] = [];

  constructor() {
    super();
  }

  public static fromObject(obj) {
      return plainToClass(Job, obj);
  }

  public static fromJson(json): Job {
    return deserialize(Job, json);
  }

  public static fromJsonArray(json): Job[] {
    return deserializeArray(Job, json);
  }

}



